.btn,
.button,
.wp-block-button__link {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 14px 18px;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  white-space: nowrap;
  transition: all 0.2s ease;
}
.btn span,
.button span,
.wp-block-button__link span {
  position: relative;
  padding-right: 15px;
}
.btn span:after,
.button span:after,
.wp-block-button__link span:after {
  content: '';
  position: absolute;
  -webkit-mask-image: url('../../img/arrow.svg');
  -webkit-mask-size: contain;
  -webkit-mask-repeat: no-repeat;
  mask-image: url('../../img/arrow.svg');
  mask-size: contain;
  mask-repeat: no-repeat;
  transition: background-color 0.2s ease;
  top: 61%;
  transform: translateY(-50%);
  right: 0;
  height: 8px;
  width: 10px;
}
.btn--primary,
.button--primary,
.mepr-white-popup .mepr-btn {
  background-color: #B22029;
  color: #ffffff;
  border: 1px solid #B22029;
}
.btn--primary span:after,
.button--primary span:after,
.mepr-white-popup .mepr-btn span:after {
  background-color: #ffffff;
}
.btn--primary:hover,
.button--primary:hover,
.mepr-white-popup .mepr-btn:hover {
  background-color: transparent;
  color: #B22029;
}
.btn--primary:hover span:after,
.button--primary:hover span:after,
.mepr-white-popup .mepr-btn:hover span:after {
  background-color: #B22029;
}
.btn--secondary,
.button--secondary,
.mepr-white-popup .mepr-confirm-no {
  background-color: #000000;
  color: #ffffff;
  border: 1px solid #ffffff;
}
.btn--secondary span:after,
.button--secondary span:after,
.mepr-white-popup .mepr-confirm-no span:after {
  background-color: #ffffff;
}
.btn--secondary:hover,
.button--secondary:hover,
.mepr-white-popup .mepr-confirm-no:hover {
  background-color: transparent;
  color: #B22029;
  border: 1px solid #B22029;
}
.btn--secondary:hover span:after,
.button--secondary:hover span:after,
.mepr-white-popup .mepr-confirm-no:hover span:after {
  background-color: #B22029;
}
.btn--back,
.button--back {
  background-color: #000000;
  color: #ffffff;
  border: 1px solid #000000;
}
.btn--back span,
.button--back span {
  padding-right: 0;
  padding-left: 15px;
}
.btn--back span:after,
.button--back span:after {
  background-color: #ffffff;
  transform: translateY(-50%) rotate(180deg);
  left: 0;
  top: 58%;
}
.btn--back:hover,
.button--back:hover {
  background-color: transparent;
  color: #000000;
}
.btn--back:hover span:after,
.button--back:hover span:after {
  background-color: #000000;
}
.btn--white {
  background-color: #ffffff;
  color: #000000;
  border: 1px solid #ffffff;
}
.btn--white:hover {
  background-color: transparent;
  color: #ffffff;
}
