@media (min-width: 1024px) {
  .site-header__nav {
    flex: 1 1 auto;
  }
  .site-header__nav .primary-menu {
    align-items: center;
  }
  .cw-header__burger {
    display: none;
  }
  .cw-header__panel {
    display: flex;
    align-items: center;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: end;
  }
}
