.mp_wrapper select,
.mp_wrapper input[type=text],
.mp_wrapper input[type=url],
.mp_wrapper input[type=email],
.mp_wrapper input[type=tel],
.mp_wrapper input[type=number],
.mp_wrapper input[type=password] {
  border-radius: 8px;
  border: 1px solid #DBE7F2;
  background: #FFF;
  padding: 15px;
  font-family: "Lato", sans-serif;
  font-size: 16px;
}
.mp_wrapper textarea:active,
.mp_wrapper select:active,
.mp_wrapper input[type=text]:active,
.mp_wrapper input[type=url]:active,
.mp_wrapper input[type=email]:active,
.mp_wrapper input[type=tel]:active,
.mp_wrapper input[type=number]:active,
.mp_wrapper input[type=password]:active,
.mp_wrapper textarea:focus,
.mp_wrapper select:focus,
.mp_wrapper input[type=text]:focus,
.mp_wrapper input[type=url]:focus,
.mp_wrapper input[type=email]:focus,
.mp_wrapper input[type=tel]:focus,
.mp_wrapper input[type=number]:focus,
.mp_wrapper input[type=password]:focus {
  border: 1px solid #B22029;
}
.mp_wrapper .mepr-submit,
.mp_wrapper .mepr-share-button,
.mp_wrapper .mepr-share-button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 14px 18px;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  white-space: nowrap;
  transition: all 0.2s ease;
  background-color: #B22029 !important;
  color: #ffffff;
  border: 1px solid #B22029 !important;
  width: 100%;
}
.mp_wrapper .mepr-submit:hover,
.mp_wrapper .mepr-share-button:hover,
.mp_wrapper .mepr-share-button:hover {
  background-color: transparent !important;
  color: #B22029 !important;
}
.mp_wrapper table.mepr-account-table th,
.mp_wrapper table.mepr-account-table td {
  background-color: #ffffff;
  padding: 10px;
}
.mp_wrapper table.mepr-account-table th {
  text-align: left;
}
.mp_wrapper table.mepr-account-table tr {
  border: 1px solid #DBE7F2;
}
.mp_wrapper table.mepr-account-table td {
  border-left: unset;
}
.mp_wrapper table.mepr-account-table .is-active,
.mp_wrapper table.mepr-account-table .is-canceled,
.mp_wrapper table.mepr-account-table .mepr-status {
  position: relative;
  padding-left: 20px;
  padding-bottom: 2px;
}
.mp_wrapper table.mepr-account-table .is-active:before,
.mp_wrapper table.mepr-account-table .is-canceled:before,
.mp_wrapper table.mepr-account-table .mepr-status:before {
  content: '';
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  height: 16px;
  width: 16px;
}
.mp_wrapper table.mepr-account-table .is-active:before,
.mp_wrapper table.mepr-account-table .is-paid:before {
  background-image: url('../../img/tick-circle-solid.svg');
}
.mp_wrapper table.mepr-account-table .is-canceled:before,
.mp_wrapper table.mepr-account-table .is-failed:before {
  background-image: url('../../img/canceled.svg');
}
.mp_wrapper table.mepr-account-table .mepr-account-actions a {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 10px 16px;
  border-radius: 8px;
  cursor: pointer;
  text-decoration: none;
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  font-size: 12px;
  text-transform: uppercase;
  white-space: nowrap;
  transition: all 0.2s ease;
}
.mp_wrapper table.mepr-account-table .mepr-account-actions a.mepr-account-renew {
  background-color: #B22029;
  color: #ffffff;
  border: 1px solid #B22029;
}
.mp_wrapper table.mepr-account-table .mepr-account-actions a.mepr-account-renew:hover {
  background-color: transparent;
  color: #B22029;
  border: 1px solid #B22029;
}
.mp_wrapper table.mepr-account-table .mepr-account-actions a.mepr-account-cancel {
  background-color: transparent;
  color: #000000;
  border: 1px solid #000000;
}
.mp_wrapper table.mepr-account-table .mepr-account-actions a.mepr-account-cancel:hover {
  background-color: #000000;
  color: #ffffff;
  border: 1px solid #000000;
}
.mepr-nav-item {
  padding: 20px;
  border-radius: 8px;
}
.mepr-nav-item a {
  color: #0B0B0B;
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  letter-spacing: 0.32px;
  line-height: 150%;
  text-transform: uppercase;
  text-decoration: none;
  position: relative;
  padding-left: 30px;
}
.mepr-nav-item a:before {
  content: '';
  position: absolute;
  mask-size: contain;
  -webkit-mask-size: contain;
  top: 50%;
  transform: translateY(-50%);
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #000000;
}
.mepr-nav-item #mepr-account-logout:before {
  mask: url('../../img/logout.svg') no-repeat center;
  -webkit-mask: url('../../img/logout.svg') no-repeat center;
}
.mepr-nav-item.mepr-active-nav-tab {
  background: #FFF;
}
.mepr-nav-item.mepr-active-nav-tab a:before {
  background-color: #175EA0;
}
.mepr-nav-item.mepr-active-nav-tab a {
  color: #175EA0;
}
.mepr-nav-item.mepr-home a:before {
  mask: url('../../img/profile.svg') no-repeat center;
  -webkit-mask: url('../../img/profile.svg') no-repeat center;
}
.mepr-nav-item.mepr-subscriptions a:before {
  mask: url('../../img/subscription.svg') no-repeat center;
  -webkit-mask: url('../../img/subscription.svg') no-repeat center;
}
.mepr-nav-item.mepr-payments a:before {
  mask: url('../../img/payment.svg') no-repeat center;
  -webkit-mask: url('../../img/payment.svg') no-repeat center;
}
.al-auth--account__header {
  margin-bottom: 24px;
}
.al-auth--account__title {
  font-size: clamp(32px, 4vw, 48px);
  text-align: left;
  margin: 0 0 10px;
}
.mepr-account-change-password {
  margin-bottom: 20px;
  display: block;
}
.al-account {
  display: flex;
  gap: 20px;
}
.al-account .mp_wrapper:nth-child(2),
.al-account #mepr-account-nav {
  background: #F5F7F9;
  border-radius: 20px;
}
.al-account #mepr-account-welcome-message {
  display: none;
}
.al-account #mepr-account-nav ul {
  display: flex;
  flex-wrap: wrap;
  gap: 0;
}
.al-account__title {
  font-size: 20px;
  font-weight: 700 !important;
  line-height: 29px;
  margin: 0 0 15px;
  text-transform: uppercase;
}
.al-account__desc {
  margin-bottom: 20px;
}
.al-account__desc-bottom {
  margin-bottom: 0;
}
.page-thank-you h1,
.page-thank-you h2:not(.elementor-heading-title) {
  display: none;
}
.al-logout__box {
  text-align: center;
}
.al-logout__actions {
  margin-top: 28px;
}
.al-logout__bottom {
  margin-top: 75px;
}
.al-logout h1 {
  font-size: clamp(32px, 3.4vw, 36px);
  margin: 0 0 12px;
}
.al-logout h2 {
  font-size: 20px;
  margin: 0 0 12px;
}
.mepr-account-terms,
.mepr-account-subscr-id,
.mepr-account-rebill {
  color: #989898;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}
@media (max-width: 767px) {
  .memberpressproduct-template-default main {
    margin-top: 50px;
    margin-bottom: 50px;
  }
  .al-auth__container {
    margin: 50px 0;
  }
  .al-auth--account__container {
    margin: 50px 0;
  }
  .al-account {
    flex-direction: column;
  }
  .al-account .mp_wrapper:nth-child(2),
  .al-account #mepr-account-nav {
    padding: 20px;
  }
  .al-logout__box {
    margin: 50px 0;
  }
}
@media (min-width: 768px) {
  .memberpressproduct-template-default main {
    margin-top: 100px;
    margin-bottom: 100px;
  }
  .page-template-page-login .al-auth__container,
  .page-template-page-forgot-password .al-auth__container {
    max-width: 570px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
    margin-bottom: 100px;
  }
  .page-template-page-login .al-auth__title,
  .page-template-page-forgot-password .al-auth__title {
    margin-bottom: 60px;
  }
  .al-auth--account__container {
    margin: 100px 0;
  }
  .al-account #mepr-account-nav {
    padding: 20px;
  }
  .al-account .mp_wrapper:first-child {
    flex: 0.3;
  }
  .al-account .mp_wrapper:nth-child(2) {
    flex: 1;
    padding: 40px;
  }
  .al-account .mp_wrapper .mp-form-row.mepr_first_name {
    margin-right: 10px;
    float: left;
  }
  .al-account .mp_wrapper .mp-form-row.mepr_last_name {
    float: right;
  }
  .al-account .mp_wrapper .mp-form-row.mepr_first_name,
  .al-account .mp_wrapper .mp-form-row.mepr_last_name {
    width: 48% !important;
    z-index: 1;
  }
  .al-account #mepr-account-nav ul {
    flex-direction: column;
  }
  .al-logout__box {
    margin: 100px 0;
  }
}
.small-pricing {
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url('../../img/bg/bg-small-price.png');
  background-color: #01050B;
  /* ===== HEADER ===== */
  /* ===== GRID ===== */
  /* ===== CARD ===== */
  /* ===== PLAN TITLE ===== */
  /* ===== PRICE ===== */
  /* ===== BEST FOR ===== */
  /* ===== FEATURES ===== */
  /* ===== CTA ===== */
}
.small-pricing__head {
  text-align: center;
  margin-bottom: 40px;
}
.small-pricing__eyebrow {
  font-family: "Barlow", sans-serif;
  font-size: 16px;
  letter-spacing: 3.2px;
  text-transform: uppercase;
  color: #FFD674;
  margin-bottom: 5px;
  font-weight: 700;
}
.small-pricing__title {
  font-size: clamp(32px, 4vw, 48px);
  color: #ffffff;
  margin: 0;
  text-transform: uppercase;
}
.small-pricing__grid {
  display: flex;
  flex-wrap: wrap;
}
.small-pricing__card {
  position: relative;
  border-radius: 20px;
  background: rgba(1, 3, 19, 0.77);
  backdrop-filter: blur(6.30000019px);
  border: 1px solid #ffffff59;
  color: #fff;
  display: flex;
  flex-direction: column;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}
.small-pricing__card:hover {
  transform: translateY(-6px);
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.45);
}
.small-pricing__card:has(.small-pricing__badge) {
  border-color: #ffdc8969;
}
.small-pricing__card:has(.small-pricing__badge) .small-pricing__cta a {
  background-color: #B22029;
  color: #ffffff;
  border: 1px solid #B22029;
}
.small-pricing__card:has(.small-pricing__badge) .small-pricing__cta a:hover {
  background-color: transparent;
  color: #B22029;
}
.small-pricing .pricing__badge-wrapper {
  left: 50%;
  transform: translateX(-50%);
}
.small-pricing__plan {
  font-family: "Barlow", sans-serif;
  font-weight: 700;
  text-align: center;
  font-size: 18px;
  letter-spacing: 1.08px;
  text-transform: uppercase;
  margin-bottom: 14px;
}
.small-pricing__price {
  font-family: "Barlow", sans-serif;
  text-align: center;
  margin-bottom: 28px;
}
.small-pricing__price-main {
  font-size: clamp(32px, 3.5vw, 40px);
  font-weight: 700;
}
.small-pricing__price-alt {
  font-size: 24px;
  font-weight: 700;
}
.small-pricing__price-suffix-mo {
  font-size: 16px;
  font-weight: 400;
  margin-left: -4px;
}
.small-pricing__price-suffix-yr {
  font-size: 24px;
  font-weight: 400;
}
.small-pricing__price-sep {
  font-size: 14px;
  font-weight: 700;
  margin: 0 8px;
}
.small-pricing__bestfor {
  background: rgba(255, 255, 255, 0.17);
  backdrop-filter: blur(9px);
  margin-bottom: 24px;
}
.small-pricing__bestfor-label {
  display: block;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 6px;
}
.small-pricing__features {
  list-style: none;
  padding: 0;
  margin: 0 0 28px;
  flex-grow: 1;
}
.small-pricing__feature {
  position: relative;
  padding-left: 26px;
  line-height: 20.8px;
  margin-bottom: 20px;
}
.small-pricing__feature:before {
  content: "";
  position: absolute;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url('../../img/shield-tick.svg');
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 12px;
  height: 14px;
}
.small-pricing__cta {
  text-align: center;
}
.small-pricing__cta a {
  width: 100%;
}
@media (max-width: 767px) {
  .small-pricing {
    padding: 50px 0;
  }
  .small-pricing__grid {
    flex-direction: column;
    gap: 20px;
  }
  .small-pricing__card-inner {
    padding: 35px 0;
  }
  .small-pricing__card-inner > :not(.small-pricing__bestfor) {
    padding: 0 25px;
  }
  .small-pricing__bestfor-inner {
    padding: 13px 25px;
  }
}
@media (min-width: 768px) {
  .small-pricing {
    padding: 100px 0;
  }
  .small-pricing__grid {
    justify-content: space-between;
  }
  .small-pricing__card {
    width: calc(100% / 2 - 17px);
    margin-bottom: 30px;
  }
  .small-pricing__card-inner {
    padding: 50px 0;
  }
  .small-pricing__card-inner > :not(.small-pricing__bestfor) {
    padding: 0 65px;
  }
  .small-pricing__bestfor-inner {
    padding: 23px 65px;
  }
}
@media (min-width: 1024px) {
  .small-pricing__card {
    width: calc(100% / 3 - 17px);
  }
}
